<template>
  <div class="page">
    <div class="weui-cells__title title">{{title}}</div>
        <div class="weui-cells">

            <div class="weui-cell weui-cell_select weui-cell_select-before">
                <div class="weui-cell__hd">
                    <select class="weui-select" name="select2">
                        <option value="1">+86</option>
                    </select>
                </div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="number" pattern="[0-9]*" placeholder="请输入号码" v-model="phone"/>
                </div>
            </div>
        </div>

        <div class="weui-btn-area">
            <a class="weui-btn weui-btn_primary" href="javascript:" @click="gohref">下一步</a>
        </div>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      title: "欢迎使用iFuture",
      phone: "",
      app: null
    };
  },
  created() {
    let phone = localStorage.getItem("phone");
    if (phone && phone.length) {
      this.phone = phone;
    }
    this.app = this.$root.$children[0];
  },
  methods: {
    gohref() {
      var myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
      if (!myreg.test(this.phone)) {
        this.app.open("请输入有效的手机号码！");
        return false;
      }
      localStorage.setItem("phone", this.phone);
      this.$router.push("/login/" + this.phone);
    }
  }
};
</script>
<style scoped>

</style>



