<template>
  <div class="page" :class="{'overh': vis}">
    <div class="weui-cells__title title">{{title}}</div>
    <div class="weui-cells">
        <div class="weui-cell weui-cell_select weui-cell_select-after">
                <div class="weui-cell__hd">
                    <label for="" class="weui-label">大学</label>
                </div>
                <div class="weui-cell__bd">
                    <select class="weui-select" name="select2" placeholder="学校" :disabled="type!='add'">
                        <option value="1">中国</option>
                        <option value="2">美国</option>
                        <option value="3">英国</option>
                    </select>
                </div>
            </div>
        <div class="weui-cell weui-cell_select weui-cell_select-after">
                <div class="weui-cell__hd">
                    <label for="" class="weui-label">专业</label>
                </div>
                <div class="weui-cell__bd">
                    <select class="weui-select myselect" name="select2" placeholder="专业名称" :disabled="type!='add'">
                        <option value="1">中国</option>
                        <option value="2">美国</option>
                        <option value="3">英国</option>
                    </select>

                    <select class="weui-select myselect" name="select2" placeholder="专业名称" :disabled="type!='add'">
                        <option value="1">中国</option>
                        <option value="2">美国</option>
                        <option value="3">英国</option>
                    </select>
                </div>
            </div>
    </div>
    <div class="weui-cells__title">大学内容</div>
    <div class="weui-cells weui-cells_form">
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <textarea class="weui-textarea" placeholder="请对你的学校发表真实的评价，吐槽的点建议如下：1.选择：为嘛来这个学校？是自己喜欢的学校吗？2.生活：校园风景美吗？学校食堂怎么样？宿舍住的舒服吗？图书馆如何?运动场地多么？社团活动咋样？大学生活的方方面面，一一说出来！3.收获：你的大学带给你最大的收获是什么？有什么需要感谢你的大学么？4.不足：在你的大学最不喜欢的是什么？有没有一些让你忍不住吐槽的点？……5.其他：大学里美好或糟糕的那些记忆，写下来，莫忘记！" rows="3"></textarea>
                        <div class="weui-textarea-counter"><span>0</span>/200</div>
                    </div>
                </div>
    </div>
    <div class="weui-cells__title">专业内容  <span @click="vis=true" class="fblue">内容样例</span></div>
    <div class="weui-cells weui-cells_form">
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <textarea class="weui-textarea" placeholder="请对你的专业发表真实的评价，吐槽的点建议如下：1.选择：为嘛读这个专业？觉得有意思吗？会转专业吗？2.课程：有意思/没意思课程有哪些？学习难度怎么样？挂科率高的有哪些课？有什么需要注意的？3.就业：师兄师姐们主要的就业行业、从事的职业有哪些？就业率怎么样？有什么需要注意的？4.生活：男女比例怎么样？单身率高不高？要经常熬夜赶报告吗？生活得苦逼吗？5.收获：学这个专业最喜欢的是什么？学到了什么？收获了什么？可以是知识、技能或者其他，比如一个好老师，一个女朋友等等。6.不足：学这个专业最讨厌啥？最不喜欢的是什么？所有你痛恨的……都写出来！7.其他：快乐或苦逼的，你能想到的，anyway，随你评论！字数最少500字" rows="3"></textarea>
                        <div class="weui-textarea-counter"><span>0</span>/200</div>
                    </div>
                </div>
    </div>

    <div class="weui-btn-area">
        <a class="weui-btn weui-btn_primary" href="javascript:">{{type=='add'?'提交': '修改'}}</a>
    </div>
        
    <div class="bodytext" v-if="vis"> <p>专业名称 ：食品科学--食品质量与安全专业；Food safety &amp; quality</p><p>吐槽与感悟：除了理论课，我们专业的实验课就是在厨房做饭，烤个蛋糕、饼干啊，做个巧克力、酸奶、罐头之类的，主要是了解每种原料的特质和一些特性。为了了解不同面粉的特性，我们会分成小组，每个组获得一种面粉，但是不告诉你是什么面粉（适于做面包的，适于烤饼干的等等）然后大家都用自己手里的面粉烤面包和饼干，然后成品感官分析，最后推测出每组是哪种面粉。</p><p>当然，我们为了专业也有很大牺牲的，比如酿酒课的感官分析，需要去系里的小酒吧，面前摆满各种酒，红酒黄酒白酒啤酒，每种都要喝，每种都要记录，一节课下来，不醉不归啊。@灰色泰迪熊 说，以后出去喝酒，醉了回来，碰到导师，冲他喷着满嘴酒气，然后自豪的说，老师，我刚自习回来。</p><p>再吐槽，很多人说：“你们学食品的是不是都会做饭啊？”于是，我们系的系服标语就是这样的“We are food scientists, We are not FxxKING chefs !!!”</p></div>
    <div class="bodytext_after" v-if="vis">
        <i @click="vis=false" class="weui-icon-cancel"></i>
    </div>
  </div>
</template>
<script>
export default {
  name: "content1",
  data() {
    return {
      title: "提交内容",
      type: "edite",
      vis: false
    };
  }
  
};
</script>
<style scoped>
.overh{
    height: 100%;
    overflow: hidden;
    position: absolute;
    width: 100%;
}
.myselect {
  width: 100px;
}
.bodytext{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    height: 80%;
    width: 80%;
    overflow: auto;
    padding: 1rem;
    z-index: 10;
    background: #fff;
    border-radius: 10px;
    color: #333;
    font-size: 14px;
}
.bodytext_after{
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background: rgba(0,0,0,.6);
}
.weui-icon-cancel{
    position: absolute;
    bottom: 5px;
    left: 48%;
    color: #fff;
    cursor: pointer;
    font-size: 30px;
}
.fblue{
    color: rgba(24, 126, 219, 1);
    float: right;
}
</style>



