<template>
  <div class="page">
    <div class="weui-cells__title title">{{title}}</div>
    <div class="weui-cells" v-if="type == 'zy'">
        <div class="weui-cell" v-for="key in [1,2,3]">
            <div class="list">
                <div class="ptit">
                    <span>专业</span> 计算机
                </div>
                <div class="ptit">
                    <span>状态</span> 已采用
                </div>
            </div>
        </div>
    </div>
    <div class="weui-cells" v-else>
        <div class="weui-cell" v-for="key in [1,2,3]">
            <div class="list">
                <div class="ptit">
                    <span>提交</span> 18163611912
                </div>
                <div class="ptit">
                    <span>专业</span> 计算机
                </div>
                <div class="ptit">
                    <span>状态</span> 已采用  <i>2018-5-16</i>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'page',
  data () {
    return {
      type: this.$route.params.type,
      title: this.$route.params.type == 'zy'? '专业' : '内容',
      phone: ''
    }
  },
  created(){
      let phone = localStorage.getItem('phone')
      if(phone && phone.length){
          this.phone = phone
      }
  },
  methods: {
      gohref(){
          localStorage.setItem('phone', this.phone)
          this.$router.push('/login/'+ this.phone)
      }
  }
}
</script>
<style scoped>
.page__bd{
    padding: 10px 15px;
    background: #fff;
    text-align: center
}
</style>



