<template>
  <div id="app">
    <router-view/>

   <!--BEGIN dialog2-->
        <div class="js_dialog" id="iosDialog2" :style="dis">
            <div class="weui-mask"></div>
            <div class="weui-dialog">
                <div class="weui-dialog__bd">{{ttt}}</div>
                <div class="weui-dialog__ft">
                    <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="close">确定</a>
                </div>
            </div>
        </div>
        <!--END dialog2-->
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      dis: 'display:none',
      ttt: ''
    }
  },
  methods:{
    open(s){
      this.dis = 'display:block'
      this.ttt = s
    },
    close(){
      this.dis = 'display:none'
    }
  }
}
</script>
<style>
.page,body {
  background-color: #f8f8f8;
}
.weui-cells__title{
  font-size: 16px;
  color: #333;
}
.weui-cells__title.title{
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.weui-vcode-btn{
  font-size: 16px;
}
.weui-cell_select-before{
  padding-right: 0
}
.weui-textarea{
  font-size: 13px;
  min-height: 160px;
}
.list{
  text-align: left;
  font-size: 14px;
  width: 100%;
  
}
.list .ptit{
  padding: 2px 0;
  position: relative;
  display: block;
}
.ptit span{
  display:inline-block;
  margin-right: 10px;
}
.ptit i{
  position: absolute;
  right: 0;
  font-style: normal
}
</style>



