<template>
  <div class="page">
    <div class="weui-cells__title title">{{title}}</div>
        <div class="weui-cells">

            <div class="weui-cell weui-cell_select weui-cell_select-before">
                <div class="weui-cell__hd">
                    <select class="weui-select" name="select2">
                        <option value="1">+86</option>
                    </select>
                </div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="number" pattern="[0-9]*" placeholder="请输入号码" v-model="phone" disabled="true"/>
                </div>
                <div class="weui-cell__ft">
                    <button class="weui-vcode-btn">获取验证码</button>
                </div>
            </div>
        </div>
        <div class="viewCode">
            <div class="weui-flex">
                <div class="weui-flex__item"><div class="placeholder"><input ref="a1" type="text" pattern="[0-9]*" v-model="a1" class="weui-input weui-inputs" maxlength="1"/></div></div>
                <div class="weui-flex__item"><div class="placeholder"><input ref="a2" type="text" pattern="[0-9]*" v-model="a2" class="weui-input weui-inputs" maxlength="1"/></div></div>
                <div class="weui-flex__item"><div class="placeholder"><input ref="a3" type="text" pattern="[0-9]*" v-model="a3" class="weui-input weui-inputs" maxlength="1"/></div></div>
                <div class="weui-flex__item"><div class="placeholder"><input ref="a4" type="text" pattern="[0-9]*" v-model="a4" class="weui-input weui-inputs" maxlength="1"/></div></div>
            </div>
            
        </div>
        <div class="weui-btn-area">
            <a class="weui-btn weui-btn_primary" href="javascript:" @click="gohref(1)">学生登录</a>
            <a class="weui-btn weui-btn_primary" href="javascript:" @click="gohref(2)">代理登录</a>
        </div>

        <div class="weui-footer weui-footer_fixed-bottom">
            <label for="weuiAgree" class="weui-agree">
                <input type="checkbox" class="weui-agree__checkbox" v-model="ck" disabled>
                <span class="weui-agree__text">
                    阅读并同意<a href="javascript:void(0);">《IFuture用户协议》</a>
                </span>
            </label>
        </div>

  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      title: '发送验证码',
      phone: this.$route.params.id,
      ck: true,
      a1: '',
      a2: '',
      a3: '',
      a4: ''
    }
  },
  methods: {
      gohref(v){
          let url = null;
          if(v == 1){
              url = '/content'
          }else{
              url = '/list'
          }
          this.$router.push({
              path: url
          })
      }
  },
  watch:{
      a1(v){
        if(v.length){
            this.$refs.a2.focus()
        }
      },
      a2(v){
        if(v.length){
            this.$refs.a3.focus()
        }else{
            this.$refs.a1.focus()
        }
      },
      a3(v){
        if(v.length){
            this.$refs.a4.focus()
        }else{
            this.$refs.a2.focus()
        }
      },
      a4(v){
        if(!v.length){
            this.$refs.a3.focus()
        }
      }
  }
}
</script>
<style scoped>
    .viewCode{
        padding: 30px 10px 20px;
        text-align: center
    }
    .weui-inputs{
        padding: 10px;
        line-height: 24px;
        font-size: 20px;
        width: 60%;
        text-align: center;
        border-radius: 0;
        border-bottom: 1px solid #187edb;
    }
</style>



